import PageButton from '../../components/button.vue';

export default {
	components: {
		PageButton,
	},

	data() {
		return {
			temp:{
				inquiryId:'',
				receptId:'',
				doctorId:'',
				doctorName:'',
				commentRate:'',
				commentContent:''
			}
		}
	},
	onLoad(options) {
		this.temp = JSON.parse(options.detail)
	},
	onShow() {
		
	},
	methods: {
		commentDoctor:function(){
			let that = this;
			that.$util.request({
				url: "/didi-patient/patientcommentdoctor/add" ,
				param: that.temp,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					setTimeout(function() {
						uni.navigateBack({
							delta:-1
						})
					}, 1000)
					uni.showToast({
						title: '发布成功',
						icon: 'success',
						duration: 2000
					})
				},
				error: function() {}
			})
		}
		
	}
}
